import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, NavParams} from 'ionic-angular';
import { User } from '../../../models/user';

@IonicPage()
@Component({
  selector: 'page-information',
  templateUrl: 'information.html'
})
export class InformationPage {

  user: User;

  constructor(public navCtrl: NavController, public navParams: NavParams, public modCtrl: ModalController) {
    this.user = navParams.get('user');
  }

  ngAfterViewInit() {


  }

  modifyInformation(title: string, value: string) {
    this.modCtrl.create('InputPage', {
      title: title,
      value: value
    }).present();
  }

  manageAddress() {
    this.navCtrl.push('AddressPage', { user: this.user });
  }
}



